import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import * as moment from 'moment'
import { Carpeta } from '../models/carpeta';

moment.locale('en');

@Injectable({
  providedIn: 'root'
})

export class carpetaService {

  constructor(private http: HttpClient) { }

  getTotal() {
    return this.http.get<any>(`${environment.url_api}getCarpetasTotal`).pipe(map(result => {
      return result.Total;
    }));
  }

  getCarpetas(): Observable<Carpeta[]> {
    return this.http.get<any>(`${environment.url_api}getCarpetas?page=0&size=100`).pipe(
      map(result => {
        return JSON.parse(JSON.stringify(result.registers)).map(item => {                
          return new Carpeta(
            item.nuc,
            item.isOpen,
            item.horaIntervencion,
            item.horaCreacion,
            item.fechaIntervencion,
            item.lugar,
            item.institucion,
            item.inicio,
            item.folio,
            item.entregado
          ); 
        });
      })
    );
  }

  getFolderxNUC(nuc: string): Observable<Carpeta[]> {

    let data = {
      nuc: nuc,
    }

    return this.http.post<any>(`${environment.url_api}getFolderXPerito`,  data).pipe(
      map(result => {
        return JSON.parse(JSON.stringify(result.Resultado)).map(item => {                
          return new Carpeta(
            item.nuc,
            item.isOpen,
            item.horaIntervencion,
            item.horaCreacion,
            item.fechaIntervencion,
            item.lugar,
            item.institucion,
            item.inicio,
            item.folio,
            item.entregado
          ); 
        });
      })
    );
  }


  updateFolder(folder: Carpeta) {
    let data = {
      horaIntervencion: folder.horaIntervencion,
      fechaIntervencion: folder.fechaIntervencion,
      lugar: folder.lugar,
      institucion: folder.institucion,
      inicio: folder.inicio,
      folio: folder.folio,
    }

    console.log(data);
    
    return this.http.put<any>(`${environment.url_api}updateFolder/${folder.nuc}`, data).pipe(map (result=> {                                              
      if(result.OK){
        return result;
      } 
    }));
  }
}