import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import * as moment from 'moment'
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { Indicio } from '../models/indicio';
import { options } from 'fusioncharts';

moment.locale('en');

@Injectable({
  providedIn: 'root'
})

export class indicioService {

  constructor(private http: HttpClient) { }

  getIndiciosTotal() {
    return this.http.get<any>(`${environment.url_api}getIndiciosTotal`).pipe(map(result => {
      return result.Total;
    }));
  }
  
  getIndiciosTotalPerito(user: string) {

    let data = {
      usuario: user
    }

    return this.http.post<any>(`${environment.url_api}getTotalIndiciosPerito`, data).pipe(map(result => {
      return result.Total;
    }));
  }

  getIndicios(): Observable<Indicio[]> {
    return this.http.get<any>(`${environment.url_api}getIndicios?page=0&size=100`).pipe(
      map(result => {
        return JSON.parse(JSON.stringify(result.UsuariosAPP)).map(item => {                
          return new Indicio(
            item.id_indicio,
            item.identificacion,
            item.descripcion,
            item.fecha,
            item.hora,
            item.documentacion,
            item.recoleccion,
            item.embalaje,
            item.latitud,
            item.longitud,
            item.lugar,
            item.foto1,
            item.naturaleza,
            item.nombreUsuario,
            item.usuario,
            item.smart_tag,
            item.foto2,
            item.nuc,
            item.escrito,
            item.fotografico,
            item.croquis,
            item.otro,
            item.especifique,
            item.entregado,
            item.estado
          ); 
        });
      })
    );
  }

  getIndiciosXNUC(indicio: string): Observable<Indicio[]> {
    let data = {
      nuc: indicio,
    }
    
    console.log(data);
    
    return this.http.post<any>(`${environment.url_api}getIndiciosxNUC/`, data).pipe(map(result => {
        return JSON.parse(JSON.stringify(result.Indicios)).map(item => {                
          return new Indicio(
            item.id_indicio,
            item.identificacion,
            item.descripcion,
            item.fecha,
            item.hora,
            item.documentacion,
            item.recoleccion,
            item.embalaje,
            item.latitud,
            item.longitud,
            item.lugar,
            item.foto1,
            item.naturaleza,
            item.nombreUsuario,
            item.usuario,
            item.smart_tag,
            item.foto2,
            item.nuc,
            item.escrito,
            item.fotografico,
            item.croquis,
            item.otro,
            item.especifique,
            item.entregado,
            item.estado
          ); 
        });
      })
    );
  }

  getIndiciosXNaturalezaAndNUC(nuc: String, naturaleza: String): Observable<Indicio[]> {
    let data = {
      nuc: nuc,
      naturaleza: naturaleza,
    }

    return this.http.post<any>(`${environment.url_api}getIndiciosXNaturalezaAndNUC/`, data).pipe(
      map(result => {
        return JSON.parse(JSON.stringify(result.IndiciosNN)).map(item => {                
          return new Indicio(
            item.id_indicio,
            item.identificacion,
            item.descripcion,
            item.fecha,
            item.hora,
            item.documentacion,
            item.recoleccion,
            item.embalaje,
            item.latitud,
            item.longitud,
            item.lugar,
            item.foto1,
            item.naturaleza,
            item.nombreUsuario,
            item.usuario,
            item.smart_tag,
            item.foto2,
            item.nuc,
            item.escrito,
            item.fotografico,
            item.croquis,
            item.otro,
            item.especifique,
            item.entregado,
            item.estado
          ); 
        });
      })
    );
  }

  updateIndicio(indicio: Indicio) {
    let data = {
      descripcion: indicio.descripcion,
      lugar: indicio.lugar,
    }

    return this.http.put<any>(`${environment.url_api}updateIndicio/${indicio.id_indicio}`, data).pipe(map (result=> {                                              
      if(result.OK){
        return result;
      } 
    }));
  }

  deleteIndicio(id_indicio: string){
    return this.http.delete<any>(`${environment.url_api}deleteIndicio/${id_indicio}`).pipe(
      map(result => {                
        return result;
      })
    );
  }
}