import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import * as moment from 'moment';
import { Vehiculo } from '../models/vehiculo';

moment.locale('en');

@Injectable({
  providedIn: 'root'
})

export class vehiculoService {

  constructor(private http: HttpClient) { }


  getRegistrosVehiculo(): Observable<Vehiculo[]> {
    return this.http.get<any>(`${environment.url_api}getRegistrosVehiculo?page=0&size=100`).pipe(
      map(result => {
        return JSON.parse(JSON.stringify(result.registers)).map(item => {                
          return new Vehiculo(
            item.id_vehiculo,
            item.identificacion,
            item.marca,
            item.tipo,
            item.linea,
            item.modelo,
            item.color,
            item.noPuertas,
            item.placas,
            item.noIdentificacion,
            item.otroDato,
            item.orientacionFrente,
            item.observacionesE,
            item.observacionesI,
            item.ninguno,
            item.idIndicio,
            item.tipoIndicio,
            item.descripcionIndicio,
            item.ubicacionIndicio,
            item.horaIndicio,
            item.observacionGeneral,
            item.traslado,
            item.horaTraslado,
            item.lugarTraslado,
            item.responsableTraslado,
            item.noAplica,
            item.fechaProcesamiento,
            item.inicioProcesamiento,
            item.finalProcesamiento,
            item.nombre,
            item.cargo,
            item.firma,
            item.latitud,
            item.longitud,
            item.fechaCreacion,
            item.nuc,
            item.folio,
            item.entregado
          ); 
        });
      })
    );
  }

  addVehiculo(datos: Vehiculo) {
    let data = {
      id_vehiculo: datos.id_vehiculo,
      identificacion: datos.identificacion,
      marca: datos.marca,
      tipo: datos.tipo,
      linea: datos.linea,
      modelo: datos.modelo,
      color: datos.color,
      noPuertas: datos.noPuertas,
      placas: datos.placas,
      noIdentificacion: datos.noIdentificacion,
      otroDato: datos.otroDato,
      orientacionFrente: datos.orientacionFrente,
      observacionesE: datos.observacionesE,
      observacionesI: datos.observacionesI,
      ninguno: datos.ninguno,
      idIndicio: datos.idIndicio,
      tipoIndicio: datos.tipoIndicio,
      descripcionIndicio: datos.descripcionIndicio,
      ubicacionIndicio: datos.ubicacionIndicio,
      horaIndicio: datos.horaIndicio,
      observacionGeneral: datos.observacionGeneral,
      traslado: datos.traslado,
      horaTraslado: datos.horaTraslado,
      lugarTraslado: datos.lugarTraslado,
      responsableTraslado: datos.responsableTraslado,
      noAplica: datos.noAplica,
      fechaProcesamiento: datos.fechaProcesamiento,
      inicioProcesamiento: datos.inicioProcesamiento,
      finalProcesamiento: datos.finalProcesamiento,
      nombre: datos.nombre,
      cargo: datos.cargo,
      firma: datos.firma,
      latitud: datos.latitud,
      longitud: datos.longitud,
      fechaCreacion: datos.fechaCreacion,
      nuc: datos.nuc,
      folio: datos.folio,
      entregado: datos.entregado
    }

    console.log(data);

    return this.http.post<any>(`${environment.url_api}addNewVehiculo`, data).pipe(
      map(result => {      
        return result.Vehiculo;
      })
    );  
  }

}